    const handleClick = (tab, event) => {
      console.log(tab);
      console.log(event);
    };
    const tocollect_money = () => {
      // proxy.$refs.collection.changestatus()
      proxy.$router.push({
        path: "/Collection_new",
        query: {
          obj: {},
          isedit: 0,
          isouter: 1,
        },
      });
    };
    const go_sign = () => {
      proxy.$router.push({
        path: "/contract_new",
        query: {
          obj: {},
          isedit: 0,
        },
      });
      proxy.isedit = 1;
      proxy.$route.query.isedit;
    };
    const returnrouter = () => {
      proxy.$router.go(-1);
    };

    const del = () => {
      proxy.$refs.followplande.newshow(1);
    };
    onMounted(() => {
      console.log(proxy.$route);
      if (proxy.$store.state.isedit == 1) {
        proxy.isedit = 1;
      } else {
        proxy.isedit = proxy.$route.query.isedit;
      }
      proxy.isouter = proxy.$route.query.isouter;
    });
    return {
      ...toRefs(state),
      handleClick,
      tocollect_money,
      go_sign,
      returnrouter,
      del,
    };
